import React from "react";
import {Link} from "react-router-dom";
function Home() {
    return (
        <>
            <main>
                <section className="main-section">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-lg-6 feature-box">
                                <figure className="figure">
                                    <img src="image/Saswat.webp" className="figure-img img-fluid rounded main-image"
                                         alt="Saswat Mishra" />
                                        <figcaption className="figure-caption">Saswat Mishra</figcaption>
                                </figure>
                            </div>
                            <div className="col-lg-6">
                                <div className="main-content">
                                    <div><h1><span>Hello</span></h1></div>
                                    <div><span><strong>Little about Me</strong></span></div>
                                    <div><span className="text-wrap">I'm a Saswat Mishra. Currently, Studying in B.Tech.(ME) in DTU.<br/>
                                        A person which love coffee while programming and twist its brain to do some crazy stuff.</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <div className="resources-link"><Link className="resume-link btn btn-outline-light" to="resume">Resume</Link>
                    <Link className="btn btn-light projects-link" to="project">Projects</Link>
                    <Link className="btn btn-light contact-link" to="contact">Contact</Link>
                </div>
            </main>
        </>
    );
}
export default Home;
