import React from "react";
import {Link} from "react-router-dom";

function Footer() {
    return (
        <>
            <footer className="d-flex flex-wrap justify-content-between align-items-center py-3 my-4 border-top">
                <p className="col-md-3 mx-auto mb-0 text-muted align-items-center justify-content-center">
                    © 2021 Saswat Mishra<br/>Made with lots of love</p>
                <a href="mailto:saswatmishra.official@gmail.com"
                   className="col-md-3 mx-5 px-5 d-flex align-items-center justify-content-center mb-3 link-dark text-decoration-none">
                    Mail
                </a>
                <ul className="nav col-md-3 mx-5 px-5 align-items-center justify-content-end">
                    <li className="nav-item"><Link to="resume" className="nav-link px-2 text-muted">Resume</Link></li>
                    <li className="nav-item"><Link to="project" className="nav-link px-2 text-muted">Project</Link></li>
                    <li className="nav-item"><Link to="contact" className="nav-link px-2 text-muted">Contact</Link></li>
                    <li className="nav-item"><a rel="noreferrer" href="https://github.com/Tech-Muni" target={"_blank"}
                                                className="nav-link px-2 text-muted">Github</a></li>
                    <li className="nav-item"><a rel="noreferrer" href="https://twitter.com/SaswatM89911826" target={"_blank"}
                                                className="nav-link px-2 text-muted">Twitter</a></li>
                    <li className="nav-item"><a rel="noreferrer" href="https://www.linkedin.com/in/saswat-mishra-283276202/"
                                                target={"_blank"} className="nav-link px-2 text-muted">LinkedIn</a></li>
                </ul>
            </footer>
        </>
    );
}
export default Footer;