import React from "react";
function Contact(){
    return (
        <>
            <main>
                <section className="main-section">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-lg-6">
                                <h2 className="mt-3 px-1 mx-5">Contact</h2>
                                <br/>
                                <p className={"mx-5 px-1"}>Looking forward to hearing from you</p>
                            </div>
                            <div className="col-lg-6">
                                <div className="p-5">
                                    <form>
                                        <label className="form-label">
                                            First Name
                                            <input className="form-control" type="text"/>
                                        </label>
                                        <br/>
                                        <label className="form-label">
                                            Last Name
                                            <input className="form-control" type="text"/>
                                        </label>
                                        <br/>
                                        <label className="form-label">
                                            Email
                                            <input className="form-control" type="email"/>
                                        </label>
                                        <br/>
                                        <label className="form-label">
                                            Subject
                                            <input className="form-control" type="text"/>
                                        </label>
                                        <br/>
                                        <label className="form-label">
                                            Message
                                            <textarea className="form-control"/>
                                        </label>
                                        <br/>
                                        <button className="btn btn-light-outline" type="submit">Submit</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </>
    );
}
export default Contact;
