import React from "react";
import {Image} from "react-bootstrap";
function Project(){
    return (
        <>
            <main className="main-section">
                <h2 className="main-area-content">Projects</h2>
                <section className="main-section">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-lg-6">
                                <h3>Personal Blog Website</h3><span><a href="https://github.com/Tech-Muni/Saswat-Blog" rel="noreferrer" target={"_blank"}>Github</a><a target={"_blank"} rel="noreferrer" href="https://saswat-blog.herokuapp.com/">Site</a></span>
                                <p className="text-wrap">In this project I've made a personal blog
                                    website using:- Flask,
                                    Bootstrap, Postgres Sql.<br/>The main objective was to learn Flask and SQL database.
                                </p>
                            </div>
                            <div className="col-lg-6">
                                <div>
                                    <Image className={"project-image"} src={"image/project-1.webp"} alt="Image taken by me of my working project"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <hr className="short-hr"/>
                    <section className="main-section">
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h3>Personal Portfolio Website</h3> <a href="https://github.com/Tech-Muni/Stone_Paper_Scissor_Game">Flask</a><a href="https://github.com/Tech-Muni/nextjs-course-code">ReactJS</a>
                                    <p className="text-wrap">In this website I've made Two
                                        websites on same topic but using two
                                        different technologies, Flask and ReactJS.<br/>The main Objective was getting
                                            familiar with these two
                                            technologies and find which one is easier, fast, short, easy maintenance
                                            Particularly in this scenario.</p>
                                </div>
                                <div className="col-lg-6">
                                    <div>
                                        <Image className={"project-image"} src={"image/project-2.webp"} alt="Image taken by me of my working project"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
            </main>
        </>
    );
}
export default Project;
